/*** Preloader style ** */
.preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 9999999;
  background-color: #1a0e22;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url(../img/preloader.gif);
}

/* button style */

.btn {
  border: none;
  color: #fff;
  display: inline-block;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0;
  line-height: 1;
  margin-bottom: 0;
  padding: 18px 45px;
  text-align: center;
  text-transform: unset;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  vertical-align: middle;
  white-space: nowrap;
  position: relative;
  z-index: 999;
  border-radius: 0;
  min-width: 210px;
}

.btn:hover {
  color: #fff;
}

.btn:hover:before {
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

.breadcrumb > .active {
  color: #888;
}

.breadcrumb-wrap.text-left {
  text-align: center !important;
  position: relative;
}

.breadcrumb-wrap nav {
  position: relative;
  z-index: 1;
  display: inline-block;
}

.btn-after {
  width: 100%;
  height: 45px;
  border: 1px solid #707070;
  border-radius: 8px;
  margin-top: -28px;
  margin-left: 15px;
  position: absolute;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  display: none;
}

.slider-btn:hover .btn-after {
  margin-top: -50px;
  margin-left: 0px;
}

