  justify-content: space-around;

  @media (max-width: 768px) {
    flex-direction: column;
  }
}

.footer-widgets {
  display: flex;
  justify-content: center;
  gap: 10rem;
}

.footer-widget-wrapper {
  margin-bottom: 30px;
}

.footer-widget-title {
  margin-bottom: 20px;
  display: flex;
  max-width: 350px;
  > h2 {
    color: #fff;
    font-size: 24px;
    padding-bottom: 18px;
    position: relative;
  }
  > h2:before {
    background: #ffc010;
    bottom: 0;
    content: "";
    height: 4px;
    left: 0;
    position: absolute;
    width: 34px;
  }
}

.footer-social-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  width: max-content;
  > a {
    font-size: 14px;
    display: inline-block;
    width: 36px;
    height: 36px;
    border-radius: 10px;
    background-color: rgb(255, 192, 16);
    text-align: center;
    line-height: 36px;
    color: rgb(255, 255, 255) !important;
  }
  > a:hover {
    background: rgb(255, 138, 1);
    color: #fff !important;
  }
  @media (max-width: 768px) {
    flex-direction: row;
  }
  @media (max-width: 1024px) {
    width: fit-content;
  }
}

.footer-reach-out {
  @media (max-width: 768px) {
    font-size: 14px;
  }
  @media (max-width: 480px) {
    font-size: 12px;
  }
}

.footer-contact-link {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  > ul {
    > li {
      gap: 1rem;
      display: flex;
      color: #90949c;
      > i {
        margin-bottom: 4px;
      }
      > span {
        > a {
          color: #90949c;
          width: 77%;
          font-weight: 600;
          @media (max-width: 768px) {
            font-size: 14px;
          }
          @media (max-width: 480px) {
            font-size: 12px;
          }
        }
        > a:hover {
          color: #ffc010;
        }
      }
    }
  }
}

.footer-link {
  gap: 0.5rem;
  display: flex;
  flex-direction: column;
  > a {
    width: max-content;
    color: #90949c;
    font-weight: 600;
  }
  > a:hover {
    transform: translateX(5px);
    color: #ffc010;
  }
}

.footer-logo {
  width: 50%;
  height: 50%;
}

.footer-copyright-wrapper {
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  font-family: "Teko", sans-serif;
}

.footer-copyright-text {
  width: fit-content;
  margin: auto;
  > span {
    color: #ffc010;
  }
  @media (max-width: 480px) {
    font-size: 3.5vw;
  }
}
